.App {
  text-align: center;
  padding: 20px;
  padding-left: 80px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #333;
  padding: 5px;
  color: white;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 50px;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.page {
  width: 90%;
  clear: both;
  display: inline-block;
  -webkit-box-shadow: 0 0 1px 1px lightgrey;
          box-shadow: 0 0 1px 1px lightgrey;
}

.table-details {
  text-align: left;
  border-collapse: collapse;
  border-collapse: collapse;
  -webkit-box-shadow: 0px 0px 1px 0px grey;
          box-shadow: 0px 0px 1px 0px grey;
  font-size: 11pt;
  line-height: 12px;
  float:left;
  margin: 20px;
}

.table-details tr:nth-child(even){
  background-color: #ececec
}

.price-change {
  text-align: right;
}

.price-details {
  position: fixed;
  bottom: 0;
  right: 0;
}

.coin-circle {
  background: white;
  color: black;
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 100px;
  font-weight: bold;
  position: relative;
  text-align: center;
}

.coin-loading {
  position: absolute;
  top: -5px;
  left: -5px;
}

.loading {
  width: 100%;
  position: absolute;
  top: 46%;
  left: 0;
}

.sleep {
  background: lightgray;
}

.nav-btn {
  color: #a9a9a9;
  text-align: center;
  font-size: 12px;
  margin: 5px 0px;
  width: 50px;
  -ms-flex-preferred-size: 26%;
      flex-basis: 26%;
  cursor: pointer;
}
.nav-btn.active {
  color: white;
  font-weight: bold;
}

/* TradingView */
#header-toolbar-chart-styles, 
#header-toolbar-compare {
  display: none;
}


